<template>
  <div id="write_blog">
    <form action="./write_blog" method="POST" role="form">
        <legend><h1></h1></legend>

        <div class="form-group">
            <label for=""></label>
            <input name="title" v-model="title" type="text" class="form-control" id="title" autocomplete="off" placeholder="">
        </div>
        
        <div class="input-group">
            <label for=""></label>
            <textarea name="content" v-model="content" id="content" class="form-control" cols="153" rows="30" autocomplete="off" required="required"></textarea>
        </div>
        
        <button type="submit" v-bind:disabled="is_submit" v-on:click.prevent="submit" class="btn btn-primary"></button>
        <button type="submit" v-bind:disabled="is_empty" v-on:click.prevent="draft_add" class="btn btn-warning"></button>
    </form> 
  </div>
</template>


<script>

export default {
  name: 'Write_blog',
  data: function () {
    return {
      title: "",
      content: "",
      username: ""
    }
  },
  beforeCreate: function () {
    this.$http.post('./sessionGet', {}).then(function (res) {
        this.username = res.data;
    })
  },
  created: function () {  //created

  },
  methods: {
    submit: function () {
      var post = {
        title: this.title,
        content: this.content,
        writer: this.username,
        blog_date: (new Date()).getTime()
      }
      this.$http.post('./write_blog', post).then(function (res) {
        this.$router.push("/blog_list_page");
      })
    },
    draft_add: function () {
      var post = {
        title: this.title,
        content: this.content,
        writer: this.username,
        draft_date: (new Date()).getTime()
      }
      this.$http.post('./draft_add', post).then(function (res) {
        this.$router.push("/blog_list_page");
      })      
    }
  },
  computed: {
    is_submit: function () {
      if (this.title != "" && this.content != "") {
        return false;
      }
      else {
        return true;
      }
    },
    is_empty: function () {
      if (this.title != "" || this.content != "") {
        return false;
      }
      else {
        return true;
      }      
    }
  }
}
</script>


<style scoped>
  #write_blog {
    text-align: left;
  }
</style>



