<template>
  <div id="user_manager" class="text-left">
	<br>
	<div class="row clearfix">
		<div class="col-md-12 column">
			<legend></legend>
			<p>
				 <em></em>&nbsp;&nbsp; <strong></strong>
			</p>
		</div>
	</div>
	<br>
	<br>
	<br>
	<div class="row clearfix">
		<div class="col-md-12 column">
		<div style="font-size:16pt;"></div> 
			<table class="table table-hover text-left">
				<tbody>
					<tr>
						<td>
							 &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{creator.username}}
						</td>
					</tr>
					<tr>
						<td>
							 &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{creator.regist_date}}
						</td>
					</tr>
					<tr>
						<td>
							 &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{creator.email}}
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
	<br>
	<br>
	<br>
	<div class="row clearfix">
		<div class="col-md-12 column">
			<legend></legend>
			<p>
				 <em></em>&nbsp;&nbsp; <strong></strong>
                 
			</p>
		</div>
	</div>
	<br>
	<br>
	<br>
	<div class="row clearfix">
		<div class="col-md-12 column">
			<legend></legend>
			<table class="table table-hover">
				<thead>
					<tr>
						<th>
							
						</th>
						<th>
							
						</th>
						<th>
							
						</th>
						<th>
							
						</th>
						<th>
							
						</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(item, index) in manager" :key="item.id">
						<td>
							{{index + 1}}
						</td>
						<td>
							{{item.username}}
						</td>
						<td>
							{{item.regist_date | standard_date}}
						</td>
						<td>
							{{item.email}}
						</td>
						<th>
							<a role="button" href="#modal-container-14357" v-on:click="click_manager_index(index)" data-toggle="modal"></a>
						</th>
					</tr>
				</tbody>
			</table>
		</div>

		<div class="modal fade" id="modal-container-14357" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
			<div class="modal-dialog">
				<div class="modal-content">
					<div class="modal-header">
							<button type="button" class="close" data-dismiss="modal" aria-hidden="true"></button>
						<h4 class="modal-title" id="myModalLabel">
							
						</h4>
					</div>
					<div class="modal-body">
						
					</div>
					<div class="modal-footer">
							<button type="button" class="btn btn-default" data-dismiss="modal"></button> 
							<button type="button" class="btn btn-primary" v-on:click="click_manager_delete"></button>
					</div>
				</div>
				
			</div>
		</div>
	</div>
	<br>
	<br>
	<br>
	<div class="row clearfix">
		<div class="col-md-12 column">
			<form role="form">
                <legend></legend>
				<div class="form-group">
					 <label></label>
                     <input type="text" v-model="input_username" class="form-control" placeholder="" />
				</div>
                <button type="button" href="#modal-container-80547" data-toggle="modal" :disabled="is_disabled" class="btn btn-primary"></button>
                <button type="reset" class="btn btn-warning"></button>
			</form>
			
			<div class="modal fade" id="modal-container-80547" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
				<div class="modal-dialog">
					<div class="modal-content">
						<div class="modal-header">
							 <button type="button" class="close" data-dismiss="modal" aria-hidden="true"></button>
							<h4 class="modal-title" id="myModalLabel">
								
							</h4>
						</div>
						<div class="modal-body">
							<em> {{input_username}} </em>
						</div>
						<div class="modal-footer">
							 <button type="button" class="btn btn-default" data-dismiss="modal"></button> 
                             <button type="button" class="btn btn-primary" v-on:click="click_manager_add"></button>
						</div>
					</div>
					
				</div>
			</div>
		</div>
	</div>
	<br>
	<br>
	<br>
  </div>
</template>

<script>
export default {
  name: 'User_manager',
  data: function () {
      return {
		  username: "",
		  input_username: "",
		  manager_index: "",
		  manager: [
			{
			  username: "1",
			  email: "ww",
			  regist_date: 11
			},
			{
			  username: "1",
			  email: "ww",
			  regist_date: 1113437687
			}  
			],
		  creator: 	{
			  username: "1",
			  email: "ww",
			  regist_date: 11
			}
			
      }
  },
  filters: {
      standard_date: function (value) {
          return (new Date(parseInt(value))).toLocaleDateString();
      }
  },
  methods: {
	  click_manager_add: function () {
		  $('#modal-container-80547').modal('hide');
		  if (this.username != this.creator.username) {
			  alert("");
			  return;
		  }
		  var post = {
			  username: this.input_username,
			  regist_date: (new Date()).getTime()
		  }
		  this.$http.post('./manager_add', post).then(function (res) {
			  this.input_username = "";
              if (res.data == 'exist_manager') {
                  alert("");
			  }
			  else if (res.data == 'not_exist_user') {
				  alert('');
			  }
              else if (res.data == 'success'){
				  this.$http.post('./manager_get', {}).then(function (res) {
					this.manager = res.data;
					alert("");
				  })
              }
          })
	  },
	  click_manager_index: function (index) {
		  this.manager_index = index;
	  },
	  click_manager_delete: function () {
		  $('#modal-container-14357').modal('hide');
		  if (this.username != this.creator.username) {
			  alert("");
			  return;
		  }
		  var post = {
			  username: this.manager[this.manager_index].username
		  }
		  this.$http.post('./manager_delete', post).then(function (res) {
			this.$http.post('./manager_get', {}).then(function (res) {
				this.manager = res.data;
			})
		  })
	  }
  },
  created: function () {
	var post = {};
	this.$http.post('./manager_get', post).then(function (res) {
		this.manager = res.data;
	})
	this.$http.post('./creator_get', post).then(function (res) {
		this.creator = res.data[0];
	})
	this.$http.post('./sessionGet', {}).then(function (res) {
        this.username = res.data;
    })
  },
  computed: {
	  is_disabled: function () {
		  if (this.input_username != "") return false;
		  else return true;
	  }
  }
}

</script>

<style scoped>

</style>




